<template>
  <div class="nav-bar-item" @click="activeClick">
    <div :style="activeStyle">
      <slot name='item-icon' />
      <slot name='item-text' />
    </div>
  </div>
</template>

<script>
export default {
  name: 'NavBarItem',
  props: {
    path: String,
    activeColor: {
      type: String,
      default: 'white'
    },
    activebgColor: {
      type: String,
      default: 'palevioletred'
    }
  },
  data () {
    return {

    }
  },
  computed: {
    isActive () {
      return this.$route.path.indexOf(this.path) !== -1
    },
    activeStyle () {
      return this.isActive ? [{ color: this.activeColor }, { background: this.activebgColor }] : {}
    }
  },
  methods: {
    activeClick () {
      this.$router.push(this.path)
    }
  }
}
</script>

<style scoped>
  .nav-bar-item {
    width: 25%;
    flex:1;
    text-align: center;
    cursor: pointer;
    display: inline-block;
    height: 49px;
    line-height: 49px;
  }
  .iconfont {
    font-size: 24px;
  }
  .text {
    font-size: 24px;
    display: block;
  }
  .nav-bar-item:hover {
    background-color: palevioletred;
    color: #fff;
    transition: all 0.5s ease-in-out;
  }
</style>
