import Vue from 'vue'
import VueRouter from 'vue-router'

const Home = () => import('@/views/Home/Home')
const History = () => import('@/views/History/history')
const Chat = () => import('@/views/Chat/chat')
const File = () => import('@/views/File/file')

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/home',
    component: Home
  },
  {
    path: '/chat',
    component: Chat
  },
  {
    path: '/history',
    component: History
  },
  {
    path: '/file',
    component: File
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
